import React, { useEffect, useState,  } from 'react';
import {Typography, useTheme, useMediaQuery} from "@mui/material";




const GeneralSearchPage = () => {
    const [iframeKey, setIframeKey] = useState(1);
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
    const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md'));

    console.log('Mobile:', isMobile, 'Tablet:', isTablet);


    //      iframe
    const injectStyles = () => {
        const iframe = document.getElementById('toursua-iframe'); //   ID  iframe
        const iframeDocument = iframe.contentDocument || iframe.contentWindow.document;

        const styleElement = iframeDocument.createElement('style');
        styleElement.type = 'text/css';
        styleElement.appendChild(document.createTextNode(`
            /*       */
            
.tsua-tabs {
    background-color:  #9DF1DF;/*      */
    padding: 10px; /*      */
    border-bottom: 1px solid #ccc; /*      */
}

/*      */
.tsua-content {
    width: 99%;
    padding: 20px;
    background-color: #E3F6FF; /*     */
    border-radius: 5px; /*   */
    border-top: 1px solid #ccc;
}

/*     */
.tsua-price-item {
    margin: 15px;
    border: 10px solid #E3F6FF; /*    */
    padding: 15px;
    transition: box-shadow 0.3s; /*    */
    border-radius: 15px; /*   */
}

.tsua-price-item:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.6); /*    */
}

/*      */
.tsua-tabs.tsua-footer {
    border-top: 1px solid #ccc; /*   */
}

/*   ,       */
.tsua-tab-item {
    background-color: #9DF1DF;
    color: black; /*   */
    padding: 10px 15px; /*   */
    margin: 5px; /*   */
    border-radius: 4px; /*   */
    border: none; /*   */
    cursor: pointer; /*     */
    text-align: center; /*     */
    display: inline-block; /*     */
    transition: background-color 0.3s, transform 0.2s; /*   */
}

/*       */
.tsua-tab-item:hover, .tsua-tab-item:active {
    background-color: #89C9B8; /*       */
    transform: scale(1.05); /*    */
}

.tsua-tab-item:active {
    transform: scale(0.95); /*    */
}

/*   */
.tsua-hotel-wrapper,
.tsua-wrapper {
    display: none; /*   */
}

/*   */
.mCSB_container {
    position: relative; /*   */
    background-color: #E3F6FF; /*    */
}

/*       */
.mCSB_scrollTools .mCSB_draggerContainer,
.mCSB_scrollTools .mCSB_dragger {
    background-color: #E3F6FF; /*      */
    border-radius: 6px; /*   */
    width: 10px; /*  */
    opacity: 0.7; /*  */
    cursor: pointer; /*   */
}

/*        */
.mCSB_scrollTools .mCSB_dragger:hover,
.mCSB_scrollTools .mCSB_dragger:active {
    background-color: #5EB961; /*       */
    opacity: 1; /*   */
}

/*     */
.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
    background-color: #E3F6FF; /*    */
    margin: 0 auto; /*   */
}

/*     */
.mCSB_scrollTools .mCSB_dragger:active .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger:focus .mCSB_dragger_bar {
    background-color: #333333; /*     */
},

@media (max-width: 600px) { /*       */
    .general-search-page {
        margin-bottom: 35px;
    }
}

@media (min-width: 601px) {
    .general-search-page {
        margin-bottom: 0;
    }
}



        `));
        iframeDocument.head.appendChild(styleElement);
    };


    useEffect(() => {
        //       iframe   
        setIframeKey(prevKey => prevKey + 1);
    }, []);


    const iframeSrcDoc = `
    <html>
    <head>
        <style>
            body { margin: 0; width: 100%; } 
        </style>
    </head>
    <body>
        <div id="toursua-module-11033">  Tours.ua...</div>
        <script src="https://mod.tours.ua/gen/module?id=5f554e6a3a5e65f8901aa9a8649c3d0c"></script>
    </body>
    </html>
`;


    return (
        <div>
            <Typography gutterBottom variant="h5" component="div"
                        style={{
                            textAlign: 'center',
                            margin: '20px 0',
                            fontSize: isMobile ? '18px' : '25px', //      
                            fontWeight: 'bold',
                            padding: isMobile ? 2 : 5  //     
                        }}>
                    :
            </Typography>
            <div style={{ display: 'flex', justifyContent: 'center', marginBottom: isMobile ? '40px' : '0px' }}>
                <iframe
                    id="toursua-iframe"
                    sandbox="allow-scripts allow-same-origin"
                    key={iframeKey}
                    srcDoc={iframeSrcDoc}
                    onLoad={injectStyles}
                    style={{
                        width: isMobile ? '90%' : '80%', // 60%   ,     
                        height: 'auto',
                        border: 'none',
                        minHeight: isTablet ? '1000px' : '775px'
                    }}
                    title="Tours.ua Module"
                />
            </div>
        </div>
    );

}


    export default GeneralSearchPage;