import { useNavigate } from 'react-router-dom';
import Button from '@mui/material/Button';

import'./backButton.css'

const BackButton = () => {
    const navigate = useNavigate();

    return (
        <Button
            variant="contained"
            color="primary"
            onClick={() => navigate(-1)}
            style={{
                marginTop: '20px',  //   
                padding: '8px 15px',  //       
                fontSize: '18px',  //   
                color: '#FFFFFF',  //  
                backgroundColor: '#8DCBE6',  //  
                borderRadius: '4px',  //  
                textTransform: 'none'  //       
            }}
        >
            
        </Button>
    );
};

export default BackButton;
