import React from 'react';

import { Card, CardContent, Typography, useTheme, useMediaQuery } from '@mui/material';

import './infoCards.css'


const cardData = [
    {
        title: " ",
        word: "   365",
        description: "",
        example: '    2017'
    },
    {
        title: "",
        word: "   ",
        description: "     ",
        example: ' , , , '
    },
    {
        title: "/ ",
        word: "   ",
        description: " ",
        description2: "",
        example: '',
        example2: ''
    },
    {
        title: "      .",
        word: "    ,  .",
        description: "    ",
    },
];
function OutlinedCardsRow() {
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
    const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md'));

    return (
        <div style={{
            width: isMobile ? '100%' : '100%',  // 95%     
            padding: isMobile ? '0' : '0 2.5%',  //  2.5%    ,       
            margin: '0 auto',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
        }}>
            <div style={{
                display: 'flex',
                flexDirection: isMobile ? 'column' : 'row',
                flexWrap: isTablet ? 'wrap' : 'nowrap',
                alignItems: isMobile ? 'center' : 'stretch',
                justifyContent: 'center',
                width: '100%',
            }}>
                {cardData.map((card, index) => (
                    (!isMobile || index === 0) && (
                        <div key={index} style={{
                            width: isMobile ? 'calc(100% - 20px)' : isTablet ? '50%' : '25%',
                            padding: isMobile ? '10px' : '6px',
                            display: 'flex',
                            flexDirection: 'column',
                        }}>
                            <Card variant="outlined" elevation={4} style={{
                                backgroundColor: '#fffaf6',
                                boxShadow: '0px 4px 8px rgba(0,0,0,0.2)',
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'space-around',
                                height: '100%',
                                width: '100%',  // ,       
                            }}>
                                <CardContent style={{ flexGrow: 1 }}>
                                    <Typography color="text.secondary" gutterBottom variant={isMobile ? "subtitle1" : "body1"}>
                                        {card.title}
                                    </Typography>
                                    <Typography variant={isMobile ? "h6" : "h5"} component="div">
                                        {card.word}
                                    </Typography>
                                    <Typography variant="body2">
                                        {card.description}
                                        <br/>
                                        {card.example}
                                    </Typography>
                                    {card.description2 && card.example2 && (
                                        <Typography variant="body2">
                                            {card.description2}
                                            <br/>
                                            {card.example2}
                                        </Typography>
                                    )}
                                </CardContent>
                            </Card>
                        </div>
                    )
                ))}
            </div>
        </div>
    );
}




export default OutlinedCardsRow;






